<template lang="html">
  <div class="players-data">
    <div class="header-data">
      <div class="pic"></div>
      <div class="name">姓名</div>
      <div class="rebs">场均篮板</div>
      <div class="assists">场均助攻</div>
      <div class="points">场均得分</div>
    </div>
    <div class="main-data">
      <ul>
        <li class="data-list" v-for="item in players">
          <span class="s-pic"><img width="35" height="40" :src="'./static/assets/' + item.profile.playerId + '.png'" alt=""></span>
          <span class="s-name">{{item.profile.firstName}}-{{item.profile.lastName}}</span>
          <span class="s-rebs">{{item.statAverage.rebsPg}}</span>
          <span class="s-assists">{{item.statAverage.assistsPg}}</span>
          <span class="s-points">{{item.statAverage.pointsPg}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    players: {
      type: Array
    }
  }
}
</script>

<style lang="css">
  .header-data {
    height: 30px;
    background-color: #aaa;
    color: #fff;
    display: flex;
  }
  .header-data div {
    flex: 1;
    font-size: 12px;
    line-height: 30px;
    text-align: center;
  }
  .header-data .pic {
    flex: 0 0 35px;
  }
  .header-data .name {
    flex: 0 0 120px;
  }
  .data-list {
    height: 40px;
    display: flex;
  }
  .data-list:nth-of-type(even) {
    background-color: #eee;
  }
  .data-list span {
    height: 100%;
    flex: 1;
    font-size: 12px;
    line-height: 40px;
    text-align: center;
  }
  .data-list .s-pic {
    flex: 0 0 35px;
    height: 40px;
  }
  .data-list .s-name {
    flex: 0 0 120px;
  }
</style>



