<template>
  <header>
    <div class="team-info">
      <div class="logo" @click="showTeam">
        <img src="./img/CLE_logo.svg" alt="">
      </div>
      <div class="team-name">
        <p class="name">{{profile.name}}({{profile.nameEn}})</p>
        <p class="record">{{standing.wins}}<span>胜</span> - {{standing.losses}}<span>负</span></p>
      </div>
    </div>
    <div class="team-ranking">
      <h1 class="rank">常规赛排名</h1>
      <ul class="cle-rank">
        <li>场均得分<span>{{rank.ppg}}</span><sup>th</sup></li>
        <li>场均助攻<span>{{rank.apg}}</span><sup>th</sup></li>
        <li>场均篮板<span>{{rank.rpg}}</span><sup>th</sup></li>
        <li>对手得分<span>{{rank.oppg}}</span><sup>th</sup></li>
      </ul>
    </div>
    <transition name="fade">
      <div @click="closeTeam" class="team-detail" v-show="teamDetail">
        <h1 class="name">{{profile.city}} {{profile.name}}</h1>
        <div class="detail-logo">
          <div class="team-logo">
            <img width="150" height="150" src="./img/CLE_logo.svg" alt="">
          </div>
          <div class="detail-info">
            <p>{{profile.displayConference}}：#{{standing.confRank}}</p>
            <p>战绩：{{standing.wins}}-{{standing.losses}}</p>
            <p>过去10场：{{standing.last10}}</p>
            <p>教练：{{profile.coach}}</p>
        </div>
        </div>
        <div class="v-title">
      		<div class="line"></div>
      		<div class="text">球队数据</div>
      		<div class="line"></div>
      	</div>
        <div class="detail-data">
          <ul>
            <li>场数：{{season.games}}</li>
            <li>%：{{season.fgpct}}</li>
            <li>三分%：{{season.fgmPg}}</li>
            <li>罚球%：{{season.fgaPg}}</li>
            <li>场均篮板：{{season.rebsPg}}</li>
            <li>进攻：{{season.tpmPg}}</li>
            <li>防守：{{season.tppct}}</li>
            <li>场均助攻：{{season.assistsPg}}</li>
            <li>场均抢断：{{season.stealsPg}}</li>
            <li>场均盖帽：{{season.blocksPg}}</li>
            <li>场均失误：{{season.turnoversPg}}</li>
            <li>场均犯规：{{season.foulsPg}}</li>
            <li>场均得分：{{season.pointsPg}}</li>
          </ul>
        </div>
      </div>
    </transition>
  </header>
</template>

<script>
export default {
  data() {
    return {
      teamDetail: false,
      season: {},
      standing: {},
      profile: {},
      rank: {}
    }
  },
  methods: {
    showTeam() {
      this.teamDetail = true
    },
    closeTeam() {
      this.teamDetail = false
    }
  },
  created() {
    this.$http.get('./static/data.json').then((res) => {
      res = res.body
      this.season = res.payload.leagueSeasonAverage
      this.standing = res.payload.team.standing
      this.profile = res.payload.team.profile
      this.rank = res.payload.team.rank
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  * {
    margin: 0;
    padding: 0;
  }
  .cle-rank {
    list-style: none;
  }
  .cle-rank li {
    width: 25%;
    font-size: 12px;
    color: #aaa;
    text-align: center;
    float: left;
    position: relative;
    margin-bottom: 5px;
  }
  .cle-rank span {
    font-size: 14px;
    margin-left: 5px;
    font-weight: bold;
    color: #000;
  }
  .cle-rank sup{
    font-size: 12px;
    color: #000;
    font-weight: 700;
  }
  header {
    width: 100%;
    background-color: #eee;
    position: relative;
  }
  .team-info {
    display: flex;
    height: 100px;
  }
  .logo {
    flex: 0 0 100px;
  }
  .logo img {
    width: 100%;
  }
  .team-name {
    flex: 1
  }
  .team-name p {
    margin-left: 10px;
    font-size: 16px;
    font-weight: 700;
  }
  .team-name .name {
    margin: 20px 0 10px 10px;
  }
  .team-name .record span{
    font-size: 12px;
    padding-left: 5px;
    color: #aaa;
    font-weight: normal;
  }
  .team-ranking {
    background-color: #fff;
  }
  .rank {
    font-size: 12px;
    font-weight: 700;
    padding: 5px;
  }

  .team-detail {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
  }

  .team-detail .name {
    font-size: 24px;
    text-align: center;
    margin-top: 15px;
  }
  .detail-logo {
    display: flex;
  }
  .team-logo {
    flex: 0 0 150px;
  }
  .detail-info {
    flex: 1
  }
  .detail-info p {
    margin: 15px 0 0 15px;
  }
  .detail-data {
    margin-top: 20px;
    width: 90%;
    margin-left: 5%;
  }
  .detail-data ul{
    list-style: none;
  }
  .detail-data li {
    width: 50%;
    margin-top: 10px;
    float: left;
  }


  .v-title {
    display: flex;
    width: 90%;
    margin: 28px auto 24px auto;
  }
  .line {
    flex: 1;
    position: relative;
    top: -6px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }
  .text {
    padding: 0 12px;
    font-size: 14px;
    font-weight: 700;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .3s
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
</style>



