<template lang="html">
<div class="players-info">
  <div class="header-info">
    <div class="pic"></div>
    <div class="name">姓名</div>
    <div class="pos">位置</div>
    <div class="height">身高</div>
    <div class="weight">体重</div>
  </div>
  <div class="main-info">
    <ul>
      <li class="info-list" v-for="(item, index) in players" @click="open">
        <span class="s-pic"><img width="35" height="40" :src="'./static/assets/' + item.profile.playerId + '.png'" alt=""></span>
        <span class="s-name">{{item.profile.firstName}}-{{item.profile.lastName}}</span>
        <span class="s-pos">{{item.profile.position}}</span>
        <span class="s-height">{{item.profile.height}}</span>
        <span class="s-weight">{{item.profile.weight}}</span>
    </li>
    </ul>
  </div>

</div>
</template>

<script>
// import profile from 'components/profile/profile'
export default {
  data() {
    return {
      showDetail: false,
    }
  },
  props: {
    players: {
      type: Array
    }
  },
  methods: {
    open(index) {
      this.showDetail = true
      // console.log(index)
    },
    close() {
      this.showDetail = false
    }
  }
}
</script>

<style lang="css">
  .header-info {
    height: 30px;
    background-color: #aaa;
    color: #fff;
    display: flex;
  }
  .header-info div {
    flex: 1;
    font-size: 12px;
    line-height: 30px;
    text-align: center;
  }
  .header-info .pic {
    flex: 0 0 35px;
  }
  .header-info .name {
    flex: 0 0 120px;
  }
  .info-list {
    height: 40px;
    display: flex;
  }
  .info-list:nth-of-type(even) {
    background-color: #eee;
  }
  .info-list span {
    height: 100%;
    flex: 1;
    font-size: 12px;
    line-height: 40px;
    text-align: center;
  }
  .info-list .s-pic {
    flex: 0 0 35px;
    height: 40px;
  }
  .info-list .s-name {
    flex: 0 0 120px;
  }

</style>



