<template>
  <div>
    <v-header></v-header>
    <div class="nav">
        <router-link to="/info" class="players-info" tag="div">
          <p>球员信息</p>
      </router-link>
        <router-link to="data" class="players-data" tag="div">
          <p>球员数据</p>
      </router-link>
    </div>

    <router-view :players="players"></router-view>
</div>
</template>

<script>
  import header from './components/header/header'

  const ERR_OK = false
  export default {
    data() {
      return {
        players: []
      }
    },
    created() {
      this.$http.get('./static/data.json').then((res) => {
        res = res.body
        this.players = res.payload.team.players
      })
    },
    components: {
      'v-header': header
    }
  }
</script>

<style>
  .nav {
    display: flex;
    width: 100%;
    height: 30px;
    text-align: center;
    line-height: 30px;
    background-color: #eee;
    color: #666;
  }
  .players-info {
    flex: 1;
  }
  .players-data {
    flex: 1
  }
  .active {
    background-color: #aaa;
    color: #fff;
  }
</style>



